exports.omitted = function(val, length = 280, symbol = '...'){
    if(val && val.length > length){
       return val.substr(0, length) + symbol;
    }
    return val;
};
exports.endDefaulContent = function(val){
    if(!val){
       return '';
    }
    return val;
};



